<template>
  <div>
    <v-img
      :height="imageHeight"
      gradient="to top right, rgba(0,0,0,.33), rgba(0,0,0,.6)"
      :src="require('@/assets/services.webp')"
    >
      <v-card
        class="d-flex flex-column mx-auto mt-6 px-2"
        min-height="500"
        max-width="900"
      >
        <v-card-title>
          <v-tabs
            v-model="tab"
            centered
            :slider-size="3"
            :show-arrows="$vuetify.breakpoint.smAndDown"
          >
            <v-tab
              v-for="(tab, index) in tabs"
              :key="`tab-${index}`"
              active-class="font-weight-bold"
              class="bottom-border px-10"
            >
              <span class="secondary--text text-none">{{ tab.name }}</span>
            </v-tab>
          </v-tabs>
        </v-card-title>
        <v-tabs-items v-model="tab">
          <v-tab-item v-for="(tab, index) in tabs" :key="`items-${index}`">
            <v-card-title class="headline px-12">{{ tab.name }}</v-card-title>
            <v-card-text class="keep-all px-12">
              <span>{{ tab.text }}</span>
            </v-card-text>
          </v-tab-item>
        </v-tabs-items>
        <v-spacer />
        <v-tabs-items v-model="tab">
          <v-tab-item v-for="(tab, index) in tabs" :key="`actions-${index}`">
            <v-card-actions class="pa-8">
              <component @submitted="showDone()" v-bind:is="tab.component" />
            </v-card-actions>
          </v-tab-item>
        </v-tabs-items>
        <div style="height: 20px" />
      </v-card>
    </v-img>

    <DoneDialog ref="doneDialog" />
  </div>
</template>

<script>
import AutomobileDialog from './Quotes/AutomobileDialog'
import OceanDialog from './Quotes/OceanDialog'
import AirDialog from './Quotes/AirDialog'
import ImportDialog from './Quotes/ImportDialog'
import DoneDialog from './Quotes/DoneDialog'

const tabs = [
  { name: 'Ocean Freight', component: OceanDialog, value: 0, href: 'ocean-freight', text: 'Vancouver Global shipping offers sea freight services that are the best way to handle large-scale international logistics needs. We work with an established network of sea freight transport providers to ensure that your freight is transported via the most time and cost-effective means. We support full and partial container loads, and handle all customs documents for countries of origin and destination. Worldwide export consolidation, import de-consolidation and handling FCL/LCL shipments to worldwide destinations.' },
  { name: 'Automobile Shipping', component: AutomobileDialog, value: 1, href: 'automobile-shipping', text: 'Vancouver Global Shipping offers full support for your entire automotive shipping needs. We support local, highway, and international road freight transport, including solo and combined transports, oversize cargo, and partial loads. We operate through an established network of road & rail freight carriers who have been through our extensive screening process. Additionally, Vancouver Global Shipping provides a turnkey service for all air transport needs, using the world\'s leading commercial freight airlines to ensure that your vehicle arrives on time and safely.' },
  { name: 'Air Freight', component: AirDialog, value: 2, href: 'air-freight', text: 'Vancouver Global Shipping provides a turnkey service for your international air transport needs. We work with the world’s leading commercial freight airlines to ensure that your long-distance freight is delivered on-time on a consistent basis. We provide a comprehensive customs service that ensures that your air freight is accompanied by all necessary documentation for the countries of origin and destination.' },
  { name: 'Import Services', component: ImportDialog, value: 3, href: 'import-services', text: 'Vancouver Global Shipping is a global logistics provider. As economic globalization advances rapidly, companies face the vital challenge of expediting and optimizing the international distribution of products. We provide solutions to this challenge offering freight services by air, ocean, rail, road, contract logistics and supply chain management through a global network. Located in Richmond British Columbia, we cover inbound and outbound shipments from the ocean and air. We are proud to offer you the support of our freight shipping experts, who will work tirelessly to make sure that your freight shipping needs are met to your complete satisfaction. Whether you are looking for Canadian freight shipping, international freight shipping, same day or specialized freight services, we offer the most competitive rates on any LTL, FTL, and intermodal shipments in the business. Additionally, Vancouver Global Shipping offers warehousing within Canada' },
]

export default {
  components: {
    DoneDialog,
  },
  data: () => ({
    tabs,
  }),
  computed: {
    tab: {
      get() {
        let href = this.$route.query.service
        let tab = tabs.find(t => t.href == href)
        return (tab && tab.value) || tabs[0].value
      },
      set(val) {
        let query = { ...this.$route.query }
        query.service = tabs[val].href
        this.$router.push({ query: query }).catch(() => { });
      }
    },
    imageHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return '950px'
        case 'sm': return '650px'
        case 'md': return '700px'
        case 'lg': return '800px'
        case 'xl': return '800px'
        default: return '800px'
      }
    },
  },
  methods: {
    showDone() {
      this.$refs.doneDialog.show()
    },
  },
}
</script>

<style scoped>
.bottom-border {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}
</style>